import { Search } from '../models/Search';
import { bbapi } from '../shared/utils/axios';

const searchService = {
  async search(
    q: string,
    page: number = 0,
    size: number = 50,
    types: string[] = ['track', 'artist']
  ): Promise<Search> {
    const typesParam = types.join(',');
    const API_TRACK_SEARCH = `/v1/search?q=${q}&types=${typesParam}&page=${page}&size=${size}`;
    const json = await bbapi.get(API_TRACK_SEARCH);
    const data = json.data as Search;
    return data;
  },
};

export default searchService;
