import { OidcProvider, OidcSecure } from '@axa-fr/react-oidc';
import { RouteObject, createBrowserRouter } from 'react-router-dom';
import AudioProvider from '../contexts/AudioProvider';
import MainLayoutProvider from '../contexts/MainLayoutProvider';
import MainLayout from '../layouts/main-layout/MainLayout';
import PlaylistDetail from '../pages/playlist-detail/PlaylistDetail';
import TrackDetail from '../pages/track-detail/TrackDetail';
import { bbwebClientConfig } from './utils/openid-client';

const ROUTES: RouteObject[] = [
  {
    path: '/',
    element: (
      <OidcProvider configuration={bbwebClientConfig}>
        <MainLayoutProvider>
          <AudioProvider>
            <MainLayout />
          </AudioProvider>
        </MainLayoutProvider>
      </OidcProvider>
    ),
    children: [
      {
        path: 'track/:trackId',
        element: <TrackDetail />,
      },
      {
        path: 'playlist/:playlistId',
        element: (
          <OidcSecure>
            <PlaylistDetail />
          </OidcSecure>
        ),
      },
    ],
  },
];

const router = createBrowserRouter(ROUTES);

export { router };
